<template>
  <div class="search">
    <div class="top">
      <van-icon @click="$router.go(-1)" name="arrow-left" />
      <van-search
        v-model="value"
        shape="round"
        placeholder="请输入搜索关键词"
      />
    </div>
    <div class="hot" v-show="value == ''">
      <h3>热搜榜</h3>
      <ul class="musicList">
        <li v-for="(item, index) in hot" :key="index">
          <h1 class="num">{{ index + 1 }}</h1>
          <div class="info">
            <h2>{{ item.searchWord }}</h2>
            <h3>
              <span>{{ item.content }}</span>
            </h3>
          </div>
          <div class="quantity">
            <span>{{ item.score }}</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="content" v-show="value != ''">
      <ul class="musicList">
        <li v-for="item in songs" :key="item.id">
          <h1 class="num"><van-icon name="search" /></h1>
          <div class="info">
            <h2>{{ item.name }}</h2>
            <h3>
              <span>{{ item.artists[0].name }}</span>
            </h3>
          </div>
          <div class="quantity">
            <span
              ><van-icon name="play-circle-o" @click="setId(item.id)"
            /></span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { gethot, getsearch ,getSongDetail} from "../api/base";
export default {
  data() {
    return {
      value: "",
      hot: [],
      songs: [],
    };
  },
  created() {
    // 获取热门歌曲
    gethot().then((data) => {
      this.hot = data.data;
    });
  },
  methods: {
    setId(id) {
      // 播放全部歌曲
      this.$emit("get-player-id", id);
      getSongDetail({ids:id}).then(data=>{
        this.$emit("push", data.songs[0]);
      })
      
    },
  },
  watch: {
    value() {
      if (this.value == "") {
        return;
      }
      getsearch({ keywords: this.value }).then((data) => {
        this.songs = data.result.songs;
      });
    },
  },
};
</script>

